import * as React from "react";
import Layout from "../components/layout";

export default (props)=>{
    return (
        <Layout pageTitle="下載">
            <div className="mt-4 px-3 bg-warning border border-primary rounded-lg">
                <div className="row">
                    <div className="col">
                        <h3><u>Python Scripts</u></h3>
                    </div>
                </div>
                <div className="row mt-1">
                    <div className="col pb-2">
                            <h4 className="text-nowrap">Ebook Extractor</h4>
                            <h5>功能介紹:</h5>
                            <ul>
                                <li>解壓縮RAR或ZIP壓縮裡的.epub及.pdf檔案，並將解壓縮出來的檔案主檔名用原壓縮檔主檔名取代!!</li>
                                <li>適用於許多網路上下載來的電子書壓縮檔!!</li>
                            </ul>
                            
                            使用方式:<br/>
                            <ol>
                                <li>請先確認您的系統有安裝Python3!!</li>
                                <li>請至 <a href="https://bitbucket.org/Dolph_Lei/extractebk/src/master/" target="_blank">https://bitbucket.org/Dolph_Lei/extractebk/src/master/</a> 下載其內三個檔案</li>
                                <li>將您下載來的rar/zip壓縮檔拖曳至extractEbook.bat，即可在壓縮檔同目錄找到解壓縮出來的電子書檔案!!</li>
                            </ol>
                    </div>
                </div>
            </div>

            <div class="mt-4 px-3 bg-warning border border-primary rounded-lg">
                <div className="row">
                    <div className="col">
                        <h3><u>PaintShop Pro Scripts</u></h3>
                    </div>
                </div>
                <div className="row mt-1">
                    <div className="col pb-2">
                            請直接造訪 <a href="https://github.com/dolphlei/dolphs_psp_scripts" target="_blank">https://github.com/dolphlei/dolphs_psp_scripts</a>下載!!<br/>
                            所有Script說明請詳參照該頁README說明!!
                    </div>
                </div>
            </div>
        </Layout>
    );
}